import React from 'react';
import { connect } from 'react-redux'
import { ingCustomReset } from '../../../ings/ings-actions'
import './category-aside.scss'
import './category-aside-custom.scss'

const stateToProps = state => {
    const { max, total, left, ingsOrder } = { ...state.drinks.drinkCustom }
    return {
        ings: state.drinks.ings,
        left,
        max,
        total,
        ingsOrder
    };
};

function dispatchToProps(dispatch) {
    return {
        ingsReset: () => dispatch(ingCustomReset())
    };
}

function CategoryAsideCustom({ ings, ingsReset, max, total, ingsOrder }) {

    const levelHeight = 15 + (total / max) * 70
    const ingsOrderReversed = ingsOrder.map(io => io)
    ingsOrderReversed.reverse()

    return (<div className='cat-aside-custom'>

        <div className='drink-custom-glass'>
            {/* <span className='drink-custom-glass'>{total + ' / ' + max + ' | ' + left}</span> */}
            <ul style={{ height: levelHeight ? levelHeight + '%' : levelHeight }}
                className='drink-custom-glass-level'>
                {ingsOrderReversed.map(ingId => {

                    return (<li key={'d-c-g-' + ingId}
                        className='drink-custom-glass-level'>
                        {ings[ingId].name}
                    </li>)
                })}

            </ul>

            <svg className='drink-custom-glass drink-custom-glass1'
                viewBox='0 0 240 240'>
                <use xlinkHref='#drink-custom-glass1'></use>
            </svg>

            <svg className='drink-custom-glass drink-custom-glass2'
                viewBox='0 0 240 240'>
                <use xlinkHref='#drink-custom-glass2'></use>
            </svg>

            <svg className='drink-custom-glass drink-custom-glass3'
                viewBox='0 0 240 240'>
                <use xlinkHref='#drink-custom-glass3'></use>
            </svg>

            <svg className='drink-custom-glass drink-custom-glass-bg'
                viewBox='0 0 240 240'>
                <use xlinkHref='#drink-custom-glass-bg'></use>
            </svg>
        </div>

        <button onClick={() => ingsReset()} className='drink-custom-glass'>Resetuj</button>
        <button className='drink-custom-glass'>ZrÃ³b drinka</button>

    </div>)
}

export default connect(stateToProps, dispatchToProps)(CategoryAsideCustom)